body {
  font-family: Arial;
  background: #f4f6f8;
}

.container {
  width: 80%;
  margin: auto;
  text-align: center;
}

.job-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
  gap: 20px;
}

.card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0,0,0,0.1);
}

button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
}

.search {
  padding: 10px;
  width: 50%;
  margin: 20px;
}

.pagination button {
  margin: 5px;
}